import Banner from '../components/Banner';
import styles from './About.module.css';

export default function About() {
  return (
    <div className={styles.aboutPage}>
      <Banner text="Despre Noi" />

      <div className={styles.aboutContent}>
        <h2>Cine suntem</h2>
        <p>
          La <strong>Gentlecut</strong>, credem c frumuseea i ngrijirea personal
          sunt mai mult dect simple servicii  sunt o experien. Echipa noastr
          de profesioniti este dedicat s ofere cele mai bune servicii de
          coafor, cosmetic i ngrijire personal ntr-un mediu prietenos i relaxant.
        </p>

        <h2>Misiunea noastr</h2>
        <p>
          Ne dorim s aducem un zmbet pe chipul fiecrui client i s oferim o
          experien de rsf i relaxare. Punem accent pe calitate,
          profesionalism i atenia la detalii, pentru ca tu s te simi mereu
          ncreztor(are) i ngrijit().
        </p>
      </div>
    </div>
  );
}
